footer{
    padding-top:10px;
    min-height:40px;
    border-top:1px solid var(--bg-color);
    transition: background-color .5s ease-in-out;
    color:var(--text-color);
    /* text-align:center; */
}

footer .container{
    display:flex;
    flex-flow:row wrap;
    justify-content:space-around;
    padding-top:0;
    padding-bottom:5px;
}

.portfolio-links
{
    display:flex;
    flex-direction:row;
    align-items:center;
}

.portfolio-links a,
.portfolio-links a:visited{
    color: var(--text-color);
    transition: color 0.5s ease-in-out;
    font-size: 2.7rem;
    margin: 0 5px;
}

.portfolio-links a:hover,
.portfolio-links a:focus,
.portfolio-links a:active{
    color:rgb(82, 185, 226);
}
